
  .menu{
    position: relative;
  }
  .menu__item:last-child{
    margin-bottom: 0;
  }
  .menu__item i{
    font-size: 18px !important;
    color: #182039;
    margin-right: 10px;
    margin-left: 10px;
    
  }
  .menu__link{
    display: flex;
    padding: 20px 20%;
    color: #63656b;
    font-size: 16px;
    font-weight: 600;
    transition: all .15s ease;
  }
  .menu__link:hover{
    background-color: #f5f5f5;
    color: #63656b;
  }
  @media screen and (max-width:768px){
    .menu{
      display: flex;
      justify-content: space-around;
    }
    .menu__link{
      display: flex;
      justify-content: center;
      align-items: centers;
    }
    .menu__item span{
      display: none;
    }
  }